import React from 'react'
import './Product.css';

class Product extends React.Component {   
 
    state = {
        submitButtonText: "Add",
        defaultCategory: "--Select Category--",
        PrevName:"",
        PName:"",
        PCategroy:"--Select Category--",
        PDesc:"",
        PPrice:"",
        NameError:"",
        CategoryError:"",
        DescError:"",
        PriceError:"",
        data:null
    };

    NameChangeHandler = event => {
        this.setState({ NameError: ""});
        this.setState({ PName : event.target.value});
      }

    DescChangeHandler = event => {
        this.setState({ DescError: ""});
        this.setState({ PDesc : event.target.value});
      }

    PriceChangeHandler = event => {
        const amount = event.target.value;
        if (!amount || amount.match(/^\d{1,}(\.\d{0,2})?$/)) {
            this.setState({ PriceError: ""});
            this.setState({ PPrice : event.target.value});
        }
        else{
            this.setState({ PriceError: "Please enter price in digits upto 2 decimal"});
            this.setState({ PPrice : ""});
        }
      }

    handleCategroyChange  = event => {
        if (event.target.value !== this.state.defaultCategory )
           this.setState({CategoryError: "" });
        this.setState({ PCategroy: event.target.value });
      }

      handleReset = event => {
        event.preventDefault();
        this.resetFields();
      }

      resetFields = () => {
        this.setState({submitButtonText: "Add"});
        this.setState({ PName: ""});
        this.setState({ PCategroy: this.state.defaultCategory});
        this.setState({ PDesc: ""});
        this.setState({ PPrice: ""});
        this.setState({ NameError: ""});
        this.setState({ CategoryError: ""});
        this.setState({ DescError: ""});
        this.setState({ PriceError: ""});
      }

      editList = (name) => {
        var array = [...this.state.data]; // make a separate copy of the array
        array = array.filter(x=> x.name === name);
        this.setState({PrevName:array[0].name});
        this.setState({PName:array[0].name});
        this.setState({PCategroy:array[0].category});
        this.setState({PDesc:array[0].desc});
        this.setState({PPrice:array[0].price});
        this.setState({submitButtonText: "Edit"});
      }

      deleteList = (name) => {
        var array = [...this.state.data]; // make a separate copy of the array
        array = array.filter(x=> x.name !== name);
        this.setState({data: array});
      }

      handleSubmit = event => {
        event.preventDefault();
        const valid = this.validate();
        if(valid)
        {
          if ( this.state.submitButtonText === "Add")  
          {
           if( this.state.data === null)
            { let newList = [{name:this.state.PName,category:this.state.PCategroy,
              desc:this.state.PDesc,price:this.state.PPrice,}];
              this.setState(
                { data:[...newList]}
              )
            }
            else
            {
              
              let newList = {name:this.state.PName,category:this.state.PCategroy,
                desc:this.state.PDesc,price:this.state.PPrice,};
              
              let newdataList = [...this.state.data];
              newdataList.push({...newList});

              this.setState(
                { data:[...newdataList]}
              );
            }
            
          }
          else{
            let objIndex = this.state.data.findIndex((obj => obj.name === this.state.PrevName));
            let updateItem = [...this.state.data];
            updateItem[objIndex].name = this.state.PName
            updateItem[objIndex].desc = this.state.PDesc
            updateItem[objIndex].category = this.state.PCategroy
            updateItem[objIndex].price = this.state.PPrice
            this.setState({updateItem});


          }
          this.resetFields(); 
        }
      }

      validate()
      {
          let result = true;
          if (!this.state.PName.trim())
          {
           this.setState({NameError: "Name can not be blank." });
           result = false;
          }
          if (this.state.PCategroy  === this.state.defaultCategory )
          {
           this.setState({CategoryError: " Please select category." });
           result = false;
          }
          if (!this.state.PDesc.trim())
          {
           this.setState({DescError: "Description can not be blank." });
           result = false;
          }
          if (!this.state.PPrice.trim())
          {
           this.setState({PriceError: "Price can not be blank." });
           result = false;
          }

          return result;
      }


    render () {
        return (
            <div className="Product" >
              <div><label> Product Name</label>
              <input type="text" onChange={this.NameChangeHandler.bind(this)} value={this.state.PName}  ></input>
              { this.state.NameError ? ( <span className="error" > {this.state.NameError}  </span>) : null }
               </div>              
              <div>
                  <label> Product Category</label>
                  <select value={this.state.PCategroy} onChange={this.handleCategroyChange.bind(this)}>
                    <option value={this.state.defaultCategory}  >{this.state.defaultCategory} </option>
                    <option value="Footwear">Footwear</option>
                    <option value="Sportswear">Sportswear</option>
                    <option value="Menswear">Menswear</option>
                    <option value="Womenswear">Womenswear</option>
                    </select> 
                    { this.state.CategoryError ? ( <span className="error" > {this.state.CategoryError}  </span>) : null }             
              </div>

              <div><label> Product Description</label>
              <input type="text" onChange={this.DescChangeHandler.bind(this)} value={this.state.PDesc}   ></input>
              { this.state.DescError ? ( <span className="error" > {this.state.DescError}  </span>) : null }
              </div>

              <div><label> Product Price</label>
              <input type="text" onChange={this.PriceChangeHandler.bind(this)}  value={this.state.PPrice}   ></input>
              { this.state.PriceError ? ( <span className="error" > {this.state.PriceError}  </span>) : null }
              </div>

              <div>
                <input type="submit" value={this.state.submitButtonText} onClick={this.handleSubmit} />
                <input  type="submit" value="Reset" onClick={this.handleReset} /> </div>

                { this.state.data ? (
                    <table>
                    <thead>
                      <tr>
                        <th>Product Id</th>
                        <th>Product Name</th>
                        <th>Product Category</th>
                        <th>Product Description</th>
                        <th>Product Price</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                    {this.state.data.map((rowData, index) => (
                        <tr>
                            <td scope="row"> <label>{index+1}</label></td>
                            <td><label>{rowData.name}</label> </td>
                            <td><label>{rowData.category}</label> </td>
                            <td><label>{rowData.desc}</label> </td>
                            <td><label>{rowData.price}</label> </td>
                            <td><a href="#" onClick={this.editList.bind(this, rowData.name)} >Edit</a>
                            <a href="#" onClick={this.deleteList.bind(this, rowData.name)} >Delete</a></td>
                        </tr>
                
                ))}
                    </tbody>
                  </table>
                 ) : null }
                
             
            </div>
        );
    }
}

export default Product



